.github {
  margin: 10rem 0rem;
  padding: 1rem;
  display: flex;
  justify-content: center;
  /* border: 1px solid #dc1010; */
}
.container {
  /* width: 50%; */
  /* margin: auto; */
  display: flex;
  flex-direction: column;
  gap: 2rem;
  align-items: center;
}
.container img {
  width: 100%;
}
.top {
  display: flex;
  gap: 1rem;
}
@media only screen and (max-width: 900px) {
  .top {
    flex-direction: column;
  }
}
